body {
  font-family: 'Noto Sans', sans-serif; }

html {
  -ms-overflow-style: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

body::-webkit-scrollbar {
  width: 0 !important; }

h1 {
  white-space: pre-line;
  font-size: 1.9rem; }

h5 {
  font-size: 1.1rem; }

.App {
  min-height: 101vh;
  overflow: hidden;
  padding: 5px;
  background: #1a1a1a; }

ul:focus {
  outline: none; }

li:focus {
  outline: none; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

.guide-msg {
  position: absolute;
  top: 25px;
  right: 1%;
  font-size: 0.8rem;
  color: #ebe6e6; }

.react-msg {
  position: absolute;
  top: 1%;
  right: 1%;
  font-size: 0.8rem;
  color: #ebe6e6; }

.touch-guide {
  font-size: 0.8rem; }

.to-top {
  background: none;
  color: #fefefe;
  border: none;
  width: 100%;
  height: 8%;
  position: absolute;
  top: 1%;
  left: 0%;
  cursor: pointer; }

