  margin-bottom: 5px;
  text-align: center;
  font-size: 1.6rem; }

.project-title-p3 {
  margin-bottom: 5px;
  text-align: center;
  font-size: 1.6rem; }

@media screen and (min-width: 0px) and (max-width: 767px) {
  .project-title {
    margin: 200px 0 5px 0;
    text-align: center;
    font-size: 1.6rem; }
  .project-title-p3 {
    margin: 140px 0 5px 0;
    text-align: center;
    font-size: 1.6rem; } }

.sub-title {
  text-align: center;
  padding-bottom: 10px;
  font-size: 1.2rem; }

.embed-responsive {
  margin-bottom: 10px; }

.info {
  text-align: left; }

.content-title {
  padding-top: 10px; }

.content-text-summary {
  margin-bottom: 0;
  line-height: normal;
  line-height: initial; }

.btn-wrapper {
  text-align: center; }

button {
  cursor: pointer; }

.btn-info-link {
  margin-right: 10px; }

.content-text-stacks {
  line-height: 1.6; }

@media screen and (min-width: 0px) and (max-width: 533px) {
  .btn-info-link {
    width: 38px;
    overflow: hidden; }
    .btn-info-link .fa {
      margin-right: 20px; } }

.running-emoji {
  transform: translateX(-100%);
  animation: run 10s linear infinite;
  transition-delay: 0s;
  margin-bottom: 3px;
  font-size: 1.3rem; }

@keyframes run {
  0% {
    transform: translateX(-50%); }
  100% {
    transform: translateX(50%); } }

